body {
  margin: 0;
  padding: 0;
  font-family: 'Courier New', Courier, monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(29,29,29);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.bg-color{
    background-color: black;
}

.text-color{
    color: white;
}
.container {
    position: relative;
  }
  
  .image {
    display: block;
    width: 100%;
    height: auto;
  }
  
  .overlay {
    position: absolute;
    top: -8px;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    -webkit-transition: .5s ease;
    transition: .5s ease;
    background-color: #008CBA;
  }
  
  .container:hover .overlay {
    opacity: 1;
  }
  
  .text {
    color:white;
    font-size: 20px;
    position: absolute;
    text-align: center;
    display: flex;
    flex-direction: column;
  }
  
.btn{
    border-radius: 5px;
    width: 10rem;
    height: 5rem;
    background-color: #333;
    color:white;
    cursor:pointer;
    border-color:rgb(162, 162, 248);
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 0px 5px 2px white inset,0px 0px 5px 2px white; 
    
}


.position{
    overflow: hidden;
    background: -webkit-linear-gradient(left, rgb(29, 29, 29), #fff, rgb(29, 29, 29));
    background: linear-gradient(90deg, rgb(29, 29, 29), #fff, rgb(29, 29, 29));
    background-repeat: no-repeat;
    background-size: 80%;
    -webkit-animation: animate 2s linear alternate infinite;
            animation: animate 2s linear alternate infinite;
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
}

@-webkit-keyframes animate{
    0%{
        background-position: -500%;
    }
    100%{
        background-position: 500%;
    }
}

@keyframes animate{
    0%{
        background-position: -500%;
    }
    100%{
        background-position: 500%;
    }
}


header{
    position: fixed;
    z-index: 1;
    width: 100%;
    top: 0;
    left: 0;
}


.banner{
    margin-top: 35%;
    margin-bottom: 50%;
}

  

  a:hover button .up {
      -webkit-animation:up 1s infinite linear;
              animation:up 1s infinite linear;
  }
  
  @-webkit-keyframes up {
      0%,100%{-webkit-transform: translate3d(0,10px,0);transform: translate3d(0,10px,0)}
      50%{-webkit-transform: translate3d(0,-7px,0);transform: translate3d(0,-7px,0)}
  }
  
  @keyframes up {
      0%,100%{-webkit-transform: translate3d(0,10px,0);transform: translate3d(0,10px,0)}
      50%{-webkit-transform: translate3d(0,-7px,0);transform: translate3d(0,-7px,0)}
  }

  a:hover .spin {
      -webkit-animation: spin 0.3s 2 linear;
              animation: spin 0.3s 2 linear;
  }
  @-webkit-keyframes spin {
      0%{-webkit-transform: rotateZ(0deg);transform: rotateZ(0deg)}
      100% {-webkit-transform: rotateZ(360deg);transform: rotateZ(360deg)}
  }
  @keyframes spin {
      0%{-webkit-transform: rotateZ(0deg);transform: rotateZ(0deg)}
      100% {-webkit-transform: rotateZ(360deg);transform: rotateZ(360deg)}
  }



